// 本次專案的
.color-pri {
    color: $color-pri;
}
.color-sec {
    color: $color-sec;
}
.color-dark {
    color: $color-dark;
}
.container-lg {
    max-width: 1640px;
    margin: 0 auto;
    padding: 0 50px;
}
html{
    font-size: 16px;
}
.title-sb{
    font-size: 72px;
}
.title-lg{
    font-size: 64px;
}
.title-md{
    font-size: 48px;
}
.title-sm{
    font-size: 36px;
}
// 時常固定使用的
* {
    box-sizing: border-box;
}

section {
    width: 100%;
}

.container {
    max-width: 1200;
    margin: 0 auto;
}

img {
    display: block;
}
a {
    text-decoration: none;
}
.flex {
    display: flex;
}
.flex-column {
    display: flex;
    flex-direction: column;
}
.flex-center-center {
    display: flex;
    justify-content: center;
    align-items: center;
}
.tac {
    text-align: center;
}

.f-wrap {
    flex-wrap: wrap;
}

// 間距
.mr-05 {
    margin-right: (.5*$cardinality)px;
}
.mr-1 {
    margin-right: (1*$cardinality)px;
}
.mr-2 {
    margin-right: (2*$cardinality)px;
}
.mr-3{
    margin-right: (3*$cardinality)px;
}

.mb-05 {
    margin-bottom: (.5*$cardinality)px;
}
.mb-1 {
    margin-bottom: (1*$cardinality)px;
}
.mb-2 {
    margin-bottom: (2*$cardinality)px;
}
.mb-3 {
    margin-bottom: (3*$cardinality)px;
}
.mb-4 {
    margin-bottom: (4*$cardinality)px;
}